.btn
{
    display: flex;
    flex-flow: row wrap;
    text-align: center;
    margin-left: 900px;
    margin-top: 150px;
    padding: 12px 32px;
    font-size: 30px;
    width:200px ;
    height: 60px;
    text-transform: uppercase;
    background: rgb(248, 217, 15);
    color: #222;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
}
